<template>
  <div>
    <div class="header">
      <span class="iconfont icon-jiantou" @click="onClickLeft"></span>
    </div>
    <div class="top-bg">
      <h3>账单详情</h3>
    </div>
    <div class="overview clearfix">
      <div class="fl">
        <h5 class="type">消费记录</h5>
        <p class="time">{{ data.createTime }}</p>
      </div>
      <div class="fr">
        <span class="num">￥{{ data.consumeMoney }}</span>
      </div>
    </div>

    <p class="ps">
      <span class="iconfont icon-tishi"></span> 信息由{{ data.createBy }}录入
    </p>

    <div class="detail-info">
      <dl class="clearfix">
        <dt class="fl">订单号</dt>
        <dd>{{ data.consumeNo }}</dd>
      </dl>

      <dl v-if="data.memberName" class="clearfix">
        <dt class="fl">会员姓名</dt>
        <dd>{{ data.memberName }}</dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">项目</dt>
        <dd>
          {{
            data.project
              ? data.project.split(",").reduce((all, curr, index) => {
                  if (index !== 0) {
                    all += ",";
                  }
                  all += projectMap[curr];
                  return all;
                }, "")
              : ""
          }}
        </dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">付款方式</dt>
        <dd>{{ paytypeMap[data.payType] || data.payType }}</dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">大工</dt>
        <dd>{{ data.stylistName }}</dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">中工</dt>
        <dd>{{ data.colorerName }}</dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">小工/助理</dt>
        <dd>{{ data.helperName }}</dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">备注</dt>
        <dd>{{ data.remark }}</dd>
      </dl>
    </div>
  </div>
</template>

<script>
import { recordOneHistory } from '@/api/index';
// eslint-disable-next-line no-unused-vars
import { mapState, mapGetters } from 'vuex';

export default {
  data() {
    return {
      data: {}
    };
  },
  computed: {
    ...mapGetters(['paytypeMap', 'projectMap'])
  },
  created() {
    recordOneHistory(this.$route.query.id).then(res => {
      if (res) {
        if (res.data.code === 0) {
          this.data = res.data.data;
        } else {
          this.$Toast(res.data.msg);
        }
      }
    });
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>

